@charset "UTF-8";
@import url('https://fonts.googleapis.com/css2?family=Orbitron&display=swap');

$font: "arial";
$bgs:aqua,lightgreen,hotpink,purple;
*{
    box-sizing: border-box;
}

body{
    background: lightcyan;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.wrap{
    width: 340px;
    padding: 30px;
    background: #fff;
    // position: absolute;
    // top: 50%;
    // left: 50%;
    // transform: translate(-50%, -50%);
    border-radius: 10px;
    box-shadow: 10px 10px 30px rgba(0,0,0,.1);

    .menu{

        // &::after{
        //     content: "";
        //     display: block;
        //     clear:both;
        // }

        display: flex;
        justify-content: space-between;

        a{
            font-size: 20px;
            color: #666;

            // &:nth-of-type(1){
            //     float: left;
            // }
            // &:nth-of-type(2){
            //     float: right;
            // }
        }
    }

    .profile{
        width: 100%;
        text-align: center;
        img{
            width: 200px;
            border-radius: 50%;
            box-shadow: 5px 15px 30px rgba(148, 225, 255,.8);
            margin-bottom: 20px;
        }
        h1{
            font: bold 22px/1 $font;
            color:#555;
            margin-bottom: 5px;
        }
        p{
            font: 12px/1 $font;
            color: #bbb;
            margin-bottom: 30px;
        }
        .btnView{
            display: flex;
            width: 180px;
            height: 32px;
            margin: 0px auto 20px;
            background: linear-gradient(45deg, aqua, skyblue);
            box-shadow: 5px 10px 20px rgba(0, 255, 255, .5);
            text-shadow: 0px 0px 5px rgab(255,255,255.6);
            // font: bold 12px/32px $font;
            font: bold 12px/1 $font;
            color: #fff;
            border-radius: 16px;
            align-items: center;
            justify-content: center;
        }
    }

    .contact{
        margin-bottom: 25px;

        li{
            width: 100%;
            padding: 10px 0px;
            border-bottom: 1px solid #eee;
            &:last-child{
                border-bottom: none;
            }
            i{
                width: 20%;
                text-align: center;
                color:#555;
                font-size: 15px;
                text-shadow: 2px 2px 2px #ddd;
            }
            span{
                font:11px/1 "orbitron";
                color:#555;
                letter-spacing: 1px;
            }
        }
    }

    .others{
        display: flex;
        justify-content: center;
        // text-align: center;
        font-size: 0px;
        margin-bottom: 20px;
        a{
            display: inline-block;
            width: 30px;
            height: 30px;
            background: gray;
            border-radius: 50%;
            margin: 0px 10px;
            opacity: .4;
            @each $bg in $bgs {
                $index:index($bgs,$bg);
                &:nth-of-type(#{$index}){
                    background: $bg;
                    @if $index<3 {
                        box-shadow: 5px 5px 10px rgba($bg,.5);    
                    }
                    @else{
                        box-shadow: 5px 5px 10px rgba($bg,.7);
                    }
                }
            }
            &.on{
                opacity: 1;
            }
        }
    }
}